import Cookies from 'js-cookie'
import router from '../router'

export default {
  namespaced: true,

  state: {
    user: {
      username: '',
      role: '',
    }
  },

  getters: {
    user: (state) => {
      return Cookies.getJSON('user')
    }
  },

  mutations: {
    setUser (state, { username, role }) {
      state.user = {
        username,
        role,
      }
    }
  },

  actions: {
    login ({ commit }, { username, password }) {
      const user = {
        username,
        role: username === 'admin' ? 'hr' : ''
      }
      Cookies.set('user', user)
      commit('setUser', user)
      router.push('/')
    },

    logout () {
      Cookies.remove('user')
      router.push('/login')
    }
  }
}
