import Vue from 'vue'
import VueRouter from 'vue-router'
import Cookies from 'js-cookie'

Vue.use(VueRouter)

const routes = [
  {
    path: '/',
    component: () => import(/* webpackChunkName: "home" */ '../views/Main.vue'),
    meta: {
      requiresAuth: true,
    },
    children: [
      {
        path: '/',
        name: 'Home',
        component: () => import(/* webpackChunkName: "home" */ '../views/Home.vue')
      },
      {
        path: '/employee',
        name: 'Employee',
        component: () => import(/* webpackChunkName: "home" */ '../views/Employee/Index.vue')
      },
      {
        path: '/employee/add',
        name: 'Add New Employee',
        component: () => import(/* webpackChunkName: "home" */ '../views/Employee/Add.vue')
      },
      {
        path: '/employee/:id/detail',
        name: 'Employee Detail',
        component: () => import(/* webpackChunkName: "home" */ '../views/Employee/Show.vue')
      },
      {
        path: '/employee/:id/edit',
        name: 'Edit Employee',
        component: () => import(/* webpackChunkName: "home" */ '../views/Employee/Edit.vue')
      },
      {
        path: '/employee/import',
        name: 'Import',
        component: () => import(/* webpackChunkName: "home" */ '../views/Employee/Import.vue')
      },
      {
        path: '/attendance',
        name: 'Attendance',
        component: () => import(/* webpackChunkName: "home" */ '../views/Attendance/Index.vue')
      },
      {
        path: '/offwork',
        name: 'Offwork',
        component: () => import(/* webpackChunkName: "home" */ '../views/Offwork/Index.vue')
      },
      {
        path: '/applicants',
        name: 'Applicants',
        component: () => import(/* webpackChunkName: "home" */ '../views/Applicant/Index.vue')
      },
      {
        path: '/broadcast',
        name: 'Broadcast',
        component: () => import(/* webpackChunkName: "home" */ '../views/Broadcast/Index.vue')
      },
    ]
  },
  {
    path: '/login',
    name: 'Login',
    component: () => import(/* webpackChunkName: "about" */ '../views/Login.vue')
  }
]

const router = new VueRouter({
  mode: 'history',
  base: process.env.BASE_URL,
  routes
})

router.beforeEach((to, from, next) => {
  if (to.matched.some(record => record.meta.requiresAuth)) {
    const user = Cookies.getJSON('user')

    if (!user) {
      next({
        path: '/login',
        query: { redirect: to.fullPath }
      })
    } else {
      next()
    }
  } else {
    next()
  }
})

export default router
